@font-face {
  font-family: "Proxima";
  src: url("./fonts/MARK SIMONSON - PROXIMA NOVA THIN.OTF") format("opentype");
  font-weight: 300;
  font-style: normal;
}

@font-face {
  font-family: "Proxima";
  src: url("./fonts/MARK SIMONSON - PROXIMA NOVA REGULAR.OTF")
    format("opentype");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "Proxima";
  src: url("./fonts/MARK SIMONSON - PROXIMA NOVA SEMIBOLD.OTF")
    format("opentype");
  font-weight: 500;
  font-style: normal;
}

@font-face {
  font-family: "Proxima";
  src: url("./fonts/MARK SIMONSON - PROXIMA NOVA BOLD.OTF") format("opentype");
  font-weight: 600;
  font-style: normal;
}

@font-face {
  font-family: "Proxima";
  src: url("./fonts/MARK SIMONSON - PROXIMA NOVA BLACK.OTF") format("opentype");
  font-weight: 800; /* Heavy */
  font-style: normal;
}

html {
  color-scheme: normal !important;
}

body {
  background: none !important;
  margin: 0;
  font-family: "Proxima", "Montserrat", -apple-system, BlinkMacSystemFont,
    "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans",
    "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100vh;
  user-select: none;
  overflow: hidden !important;
}

p {
  margin: 0;
}

#root {
  height: 100%;
}

.App {
  text-align: center;
}

.toast-inform {
  background-color: #2980b9;
}

.toast-success {
  background-color: #27ae60;
}

.toast-error {
  background-color: #c0392b;
}

.mapborder {
  position: absolute;
  bottom: 7.9%;
  left: 1.3%;
  width: 0%;
  text-align: center;
}

.square {
  bottom: 6.3%;
  width: 29vh;
  height: 18.5vh;
  border: 4px solid #bababa;
  position: absolute;
  display: inline-block;
}

@media (min-width: 800px) {
  .square {
    left: 2.5vh !important;
    bottom: 6% !important;
  }
  .circle {
    left: 3.2vh !important;
    width: 27.6vh !important;
    bottom: 6.9% !important;
  }
}

@media (width: 3840px) and (height: 2160px) {
  .square {
    left: 2.4vh !important;
    bottom: 6.1% !important;
    width: 29.3vh !important;
  }
  .circle {
    left: 3.6vh !important;
    width: 27.6vh !important;
    bottom: 6.9% !important;
  }
}

@media (width: 3440px) and (height: 1440px) {
  .square {
    left: 2.3vh !important;
    bottom: 6.1% !important;
    width: 29.3vh !important;
  }
  .circle {
    left: 3.5vh !important;
    width: 27.6vh !important;
    bottom: 6.9% !important;
  }
}

@media (width: 2560px) and (height: 1440px) {
  .square {
    left: 0.2vw !important;
    bottom: -1.8vh !important;
  }
  .circle {
    left: 0.75vw !important;
    width: 27.6vh !important;
    bottom: -1vh !important;
  }
}

@media (width: 2560px) and (height: 1080px) {
  .square {
    left: -0.25vw !important;
    bottom: -1.8vh !important;
  }
  .circle {
    left: 0.15vw !important;
    width: 27.6vh !important;
    bottom: -1vh !important;
  }
}

@media (width: 1920px) and (height: 1440px) {
  .square {
    left: 0vw !important;
    bottom: -1.9vh !important;
  }
  .circle {
    left: 0.9vw !important;
    width: 27.6vh !important;
    bottom: -1vh !important;
  }
}

@media (width: 1920px) and (height: 1200px) {
  .square {
    left: 0.1vw !important;
    bottom: -2vh !important;
  }
  .circle {
    left: 0.8vw !important;
    width: 27.6vh !important;
    bottom: -1vh !important;
  }
}

@media (width: 1920px) and (height: 1080px) {
  .square {
    left: 0.1vw !important;
    bottom: -2vh !important;
  }
  .circle {
    left: 0.9vw !important;
    width: 27.3vh !important;
    bottom: -1vh !important;
  }
}

@media (width: 1280px) and (height: 720px) {
  .square {
    left: 0.1vw !important;
    bottom: -2vh !important;
  }
  .circle {
    left: 0.9vw !important;
    width: 27.6vh !important;
    bottom: -0.9vh !important;
  }
}

.responsive {
  position: absolute !important;
}
@media (min-width: 800px) {
  .responsive {
    margin-left: 32.5vh !important;
    transform: scale(1, 1);
    bottom: 6.9vh !important;
  }
}

@media (width: 2560px) and (height: 1440px) {
  .responsive {
    margin-left: 32.5vh !important;
    transform: scale(1, 1);
    bottom: 6.1vh !important;
    font-size: x-large;
  }
}

@media (width: 1920px) and (height: 1080px) {
  .responsive {
    margin-left: 17.5vw !important;
    transform: scale(1, 1);
    bottom: 5.8vh !important;
  }
}

@media (width: 1280px) and (height: 720px) {
  .responsive {
    margin-left: 30.5vh !important;
    transform: scale(1, 1);
    bottom: 5.8vh !important;
  }
}
